body {
    display: flex; 
    font-family: $font-family; 
    flex-direction: column;
    height:100vh;
}

.container {
    padding: 0 $padding-page;
}

.content {
    @include screen(medium){
        display: flex;
        flex-direction: row;
    }
}

main {
    a {
        @include link-style();
    }

    @include screen(medium){
        padding-left: 2em;
    }
}

footer {
    @include color($background-color: $color-base);
    margin-top: auto;

    a {
        @include link-style($color: inherit);
    }
    
}

